import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL, FILES_ROOT } from "../file.constants"
import { FileParkingReturn } from "../file.types"

const createParking = async ({ filename }: { filename: string }) => {
  const { data } = await axiosClient.post<FileParkingReturn>(`${FILES_BASE_URL}${FILES_ROOT}`, {
    file_name: filename,
    connect_to: ["smart_topics"],
  })
  return data
}

export { createParking }
